import {
  GET_MYKPI_FAIL,
  GET_MYKPI_REQUEST,
  GET_MYKPI_SUCCESS,
  GET_ALLKPI_FAIL,
  GET_ALLKPI_REQUEST,
  GET_ALLKPI_SUCCESS,
  GET_FIRSTKPI_FAIL,
  GET_FIRSTKPI_REQUEST,
  GET_FIRSTKPI_SUCCESS,
  GET_SECONDKPI_FAIL,
  GET_SECONDKPI_REQUEST,
  GET_SECONDKPI_SUCCESS,
  GET_THIRDKPI_FAIL,
  GET_THIRDKPI_REQUEST,
  GET_THIRDKPI_SUCCESS,
  GET_FOURTHKPI_FAIL,
  GET_FOURTHKPI_REQUEST,
  GET_FOURTHKPI_SUCCESS,
} from "../constants/kpiConstants";

export const getKpiReducer = (state = { kpi: [] }, action) => {
  switch (action.type) {
    case GET_MYKPI_REQUEST:
      return { ...state, loading: true };
    case GET_MYKPI_SUCCESS:
      return {
        loading: false,
        success: true,
        kpi: action.payload.data,
      };
    case GET_MYKPI_FAIL:
      return { loading: false, error: action.payload };
    default:
      return state;
  }
};

